body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}

.wrapper {
  width: 400px;
  height: 600px;
}

.row {
  display: flex;
  width: 100%;
}
.button-wrapper {
    height: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #e0e1e6;
    color: #888;
    flex: 1 1;
    outline: 1px solid black;
    cursor: pointer;
}

.operator {
    background-color: #fe9241;
    color: white;
    cursor: pointer;

}
.input {
    height: 4em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #000000;
    color: wheat;
    padding: 0.5em;
    outline: 1px solid #888888;
}
.clear-button {
    height: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: gray;
    font-weight: lighter;
    font-size: 1.4em;
    color: #000;
    flex: 1 1;
    outline: 1px solid black;
    cursor: pointer;
}
